export default {
	data() {
		return {
			// modal默认不显示
			modal:null,
			// 加载显示内容
			title:"审核不通过",
			// 提交内容
			content: "",
			// 初始空
			text:"",
		}
	},
	methods: {
		show(data){
			if (typeof data === 'object' && data) {
				this.title = data.title;
			}
			this.text = "";
			this.modal = "show";
		},
		hide(){
			this.modal = null;
			this.setFoucs = true;
		},
		//确定
		submit(){
			// 触发父页面v-on:pouple方法
			let content = this.content;
			this.$emit("poupleInput",content);
		},
		// 监听输入
		monitorInput(e){
			this.content = e.detail.value;
		}
	}
}
